import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Field } from 'formik';

import Checkbox from '../Checkbox';

import styles from './styles.scss';

const CheckboxGroup = ({ options, className, name, value, onChange, userWebsiteField, style }) => (
    <div className={classNames(className, userWebsiteField && styles.pane)} style={style}>
        {options.map((option, index) => {
            const isSelected = value?.includes(option.value);

            return (
                <Field
                    as={Checkbox}
                    key={name + index}
                    builderInput
                    name={name}
                    value={option.value}
                    placeholder={option.label}
                    onChange={onChange}
                    isSelected={isSelected}
                    className={styles.checkbox}
                    style={style}
                />
            );
        })}
    </div>
);

CheckboxGroup.propTypes = {
    className: PropTypes.string,
    name: PropTypes.string,
    options: PropTypes.arrayOf(PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })).isRequired,
    onChange: PropTypes.func.isRequired,
    value: PropTypes.array,
    userWebsiteField: PropTypes.bool,
    style: PropTypes.object,
};

export default CheckboxGroup;
